.container {
	max-width: 612px;
}

input.search-location {
	border: 1px solid var(--general-2);
	height: 40px !important;
	border-radius: 4px;

	&:focus {
		outline: unset !important;
	}

	&::placeholder {
		font-style: italic !important;
		color: var(--general-2) !important;
	}
}

.search-option {
	min-height: 45px;
	height: 45px;
	border-bottom: 1px solid var(--neutral-1);
}

.bedroom-item {
	border: 1px solid var(--general-2);
	border-radius: 4px;
	margin-bottom: 24px;
}

.add-btn {
	height: 40px;
	line-height: 40px;

	& * {
		vertical-align: middle;
	}

	input {
		height: 100%;
	}
}

.round-btn {
	width: 40px !important;
	height: 40px !important;
	box-shadow: unset;
	border-radius: 50% !important;
	min-width: unset !important;

	.mat-icon {
		margin-inline-start: auto !important;
		margin-inline-end: auto !important;
	}
}

.bed-icon {
	border-radius: 50%;
	width: 35px;
	height: 35px;
	background-color: var(--info-1);
	position: relative;
	display: inline-block;

	> .mat-icon {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	}
}

.bedroom-count {
	line-height: 35px;
	color: var(--general-5);
}

.w-35px {
	width: 35px;
}

.w-600px {
	max-width: 600px;
}

.h-100 {
	height: 100% !important;
}

.radio-btn-item {
	border: 1px solid var(--general-3);
	height: 40px;
	border-radius: 4px;
	text-decoration: none;
	cursor: pointer;
}

a {
	mat-icon {
		display: inline !important;
		vertical-align: middle !important;
		margin-inline-start: 4px;
	}
}

.mw-550px {
	max-width: 550px;
}

findqo-button {
	height: 40px;
}

.w-125px {
	width: 125px;
}

.container-fluid {
	z-index: var(--z-index-place-ad-action-btn);
}

.chip-selected {
	border-radius: 4px !important;
	border: 1px solid #ff5ab8 !important;
	color: #ff5ab8;
	min-height: 40px;
	background-color: #feebf6 !important;
}

.checkbox-list {
	border: 1px solid var(--general-2);
	height: 40px;
	border-radius: 4px;
	text-decoration: none;
	cursor: pointer;
}
